.biography {
  &__photo {
    font-size: 0; }
  &__main {
    background: #666666; }
  &__container {
    display: flex; } }
.biography-text {
  margin: 0 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  &__title {
    text-align: center;
    margin-top: 30px;
    margin-bottom: 30px; }
  &__title-header {
    font-size: 61px;
    color: #ffc06f; }
  &__title-footer {
    font-size: 31px;
    color: #f6f5c5; }
  &__main {
    font-size: 16px;
    color: #fff;
    margin-bottom: 50px; }
  &__cap-letter {
    font-size: 31px;
    color: #f6f5c5;
    &-small {
      font-size: 16px; } }
  &__more {
    text-align: center;
    width: 95px;
    height: 30px;
    line-height: 30px;
    background: white;
    position: absolute;
    right: 0;
    bottom: 10px;
    text-decoration: none;
    border: 2px solid white;
    color: inherit;
    &:hover {
      border: 2px solid #ffc06f; } } }
